

.App {
  // text-align: center;
}

.grid {
  display: grid;
  grid-template-rows: repeat(3, auto);
  height: 100vh;
}

header {
  grid-area: 1/1/2/-1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: #222;
  -ms-flex-align: center;
      align-items: center;
  height: 50px;
}

.cover-image {
  grid-area: 1/1/-1/-1;
  //opacity: 0.8;
  z-index: -1;
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 0% 85%;
     object-position: 0% 85%;;
  
}

main {
  grid-area: 2/1/3/-1;
  padding: 0 35%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40px;
  padding: 30px;

}
.App-nav {
  padding: 20px;
  color: white;
}

.header-links li {
  font-size: 1em;
  display: inline-block;
  list-style-type: none;
  margin: 0;
}

.header-links li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}


.project-image {
  height: 200px;
  width: 200px;
  border-radius: 50%;

}
.project-list {
  display: -ms-flexbox;
  display: flex; 
  -ms-flex-pack: space-evenly; 
      justify-content: space-evenly;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 50px;

}

.project-detail {
  text-align: center;
}

.project-title {
  text-align: center;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
  to { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
  to { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }
}



